$sub-color: grey;
$main-color: black;
$placeholder: #555;
$font-color: #333333;

@mixin shrinkLabel {
  top: -14px;
  font-size: 12px;
  color: $placeholder;
}

.group {
  position: relative;
  margin: 25px 0;

  .form-input {
    background: none;
    background-color: white;
    color: $font-color;
    font-size: 16px;
    padding: 10px 25px 5px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid $sub-color;
    margin: 25px 0;

    &:focus {
      outline: none;
    }

    &:focus ~ .form-input-label {
      @include shrinkLabel();
    }

    &::placeholder {
      color: $placeholder;
    }
  }

  input[type='password'] {
    letter-spacing: 0.3em;
  }

  .form-input-label {
    color: $placeholder;
    font-size: 14px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    transition: 300ms ease all;

    &.shrink {
      @include shrinkLabel();
    }
  }
}