@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
/**
******** GLOBAL ********
**/

:root {
  --active-color: #c8c8c8;
  --inactive-color: #dddddd;
  --light-color: #eeeeee;
  --font-color: #333333;
  --red-color: #ff0000;
  --blue-color: #0b8ff9;
  --bg-color: #f8f8f8;
  --white-color: #ffffff;
  --border-color: #cccccc;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/**
******** LAYOUT ********
**/

.app_body__5lgNh {
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 6vw 0 0 0;
  background-color: var(--bg-color);
  justify-content: space-between;
}

.app_main__container__1zyyg {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 47.5vw;
  margin: 0 auto;
  padding: 30px;
  background-color: var(--white-color);
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: flex-start;
  flex-grow: 5;
}

@media (max-width: 1400px) {
  .app_main__container__1zyyg {
    max-width: 60vw;
  }     
}

@media (max-width: 1024px) {
  .app_main__container__1zyyg {
    max-width: 70vw;
  }     
